'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

const zodiacSigns = [
  { name: 'Áries', period: '21/03 - 19/04', symbol: '♈' },
  { name: 'Touro', period: '20/04 - 20/05', symbol: '♉' },
  { name: 'Gêmeos', period: '21/05 - 20/06', symbol: '♊' },
  { name: 'Câncer', period: '21/06 - 22/07', symbol: '♋' },
  { name: 'Leão', period: '23/07 - 22/08', symbol: '♌' },
  { name: 'Virgem', period: '23/08 - 22/09', symbol: '♍' },
  { name: 'Libra', period: '23/09 - 22/10', symbol: '♎' },
  { name: 'Escorpião', period: '23/10 - 21/11', symbol: '♏' },
  { name: 'Sagitário', period: '22/11 - 21/12', symbol: '♐' },
  { name: 'Capricórnio', period: '22/12 - 19/01', symbol: '♑' },
  { name: 'Aquário', period: '20/01 - 18/02', symbol: '♒' },
  { name: 'Peixes', period: '19/02 - 20/03', symbol: '♓' }
]

export default function Home() {
  const router = useRouter()

  const handleSelectSign = (sign: string) => {
    if (typeof window !== 'undefined') {
      localStorage.setItem('astrohoroscope-sign', sign)
      setTimeout(() => {
        window.location.href = '/horoscope'
      }, 0)
    }
  }

  const handleCompatibility = () => {
    if (typeof window !== 'undefined') {
      window.location.href = '/compatibility'
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 text-white p-4">
      <div className="max-w-4xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-3xl md:text-4xl font-bold mb-4 font-space-grotesk">
            AstroHoróscopo
          </h1>
          <p className="text-center text-base mb-6 text-gray-300">
            Escolha seu signo para descobrir seu horóscopo e características
          </p>
        </div>

        {/* Compatibility Button */}
        <div className="text-center mb-8">
          <Button
            onClick={handleCompatibility}
            className="bg-purple-600 hover:bg-purple-700 px-8 py-3 text-lg"
          >
            💕 Compatibilidade de Signos
          </Button>
        </div>
        
        {/* Compact Zodiac Grid */}
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3">
          {zodiacSigns.map((sign) => (
            <Card 
              key={sign.name}
              className="bg-slate-800/50 border-slate-700 hover:bg-slate-700/50 transition-all duration-200 cursor-pointer hover:scale-105"
              onClick={() => handleSelectSign(sign.name)}
            >
              <div className="p-4 text-center">
                <div className="text-2xl mb-2">{sign.symbol}</div>
                <h3 className="text-lg font-semibold mb-1">{sign.name}</h3>
                <p className="text-xs text-gray-400">{sign.period}</p>
                <Button 
                  className="w-full mt-3 bg-indigo-600 hover:bg-indigo-700 text-sm py-2"
                  onClick={(e) => {
                    e.stopPropagation()
                    handleSelectSign(sign.name)
                  }}
                >
                  Ver Horóscopo
                </Button>
              </div>
            </Card>
          ))}
        </div>

        <div className="text-center mt-8">
          <p className="text-sm text-gray-400">
            Clique em um signo para ver seu horóscopo diário e semanal
          </p>
        </div>
      </div>
    </main>
  )
}
